//Fonts
@font-face {
  font-family: "Hubballi-Regular";
  src: url(../../fonts/Hubballi-Regular.ttf);
}


//Header
#header {
  margin-top: -30px;
  
  @media (min-width:992px) {
    margin-top: -60px;
  }
}

// Body
body {
    margin: 0;
    background-color: $background-color;
    height: 100%;
    font-family: "Hubballi-Regular", Helvetica;
    overflow-x: hidden;
    overflow-y: scroll;
    width: 100%;
    color: white;
    padding: 10px;
  }

// Main
main {
  margin: 0 auto;
}